<template>
  <div :class="$root.themeHomeChange === '1' ? 'supervisionPlanExecution_light' : 'supervisionPlanExecution'">
    <div class="header">
      <span class="left">监督计划执行</span>
      <div class="right">
        <el-date-picker
          v-model="params.year"
          type="year"
          placeholder="选择年"
          value-format="yyyy"
          style="margin-right: 15px;"
        >
        </el-date-picker>
        <el-button
          type="primary"
          icon="el-icon-edit-outline"
          @click="planExecution(true)"
        > 计划执行 </el-button>
        <el-button
          type="warning"
          icon="el-icon-document"
          :plain="$root.themeHomeChange === '1'"
          :disabled="!(currentRow && currentRow.isCompletedRectify === 0 )"
          @click="planExecution(false)"
        > 编辑 </el-button>
        <el-button
          type="danger"
          icon="el-icon-delete"
          :plain="$root.themeHomeChange === '1'"
          :disabled="multipleSelection.length === 0"
          @click="deleteTableItem"
        > 删除 </el-button>
      </div>
    </div>
    <el-form
      :inline="true"
      class="selectBox"
      :model="params">
      <!-- <el-button type="primary" style="float: left;">生成报告</el-button> -->
      <span>
        <el-form-item label="整改时间: ">
          <el-input-number
            v-model="params.rectifyTime"
            style="width: 155px; margin-right: 15px;"
            :min="1"
            :max="12"
            size="mini"
            placeholder="整改时间(月份)"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="单位名称: ">
          <SelectTree
            style="width: 100%"
            ref="selectTreeRef"
            :props="defaultProps"
            :options="treeData"
            :value="valueId"
            :clearable="true"
            :accordion="true"
            @getValue="getValue($event)"
          />
        </el-form-item>
        <el-form-item label="领域: ">
          <el-select
            size="mini"
            v-model="params.supervisionField"
            placeholder="请选择"
            style="width: 150px;"
            clearable
            @change="queryTableList(true)"
          >
            <el-option
              v-for="(item, index) in formOption['supervisionField']"
              :label="item.label"
              :value="item.value"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
      </span>
      <span>
        <el-form-item>
          <bda-search-input
            v-model="params.keyword"
            placeholder="请输入关键字"
            @search="queryTableList(true)"
            @clear="queryTableList(true)"
            size="mini"
          >
          </bda-search-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            icon="el-icon-search"
            @click="queryTableList(true)"
          > 查询 </el-button>
        </el-form-item>
      </span>
    </el-form>
    <div class="content">
      <el-table
        :data="tableData"
        style="width: 100%"
        :height="tableHeight"
        v-loading="tableLoading"
        highlight-current-row
        @current-change="handleCurrentChange_table"
        @selection-change="handleSelectionChange"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      >
        <el-table-column
          type="selection"
          width="55"
          align="center">
        </el-table-column>
        <el-table-column
          type="index"
          label="序号"
          width="60"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="taskName"
          label="任务名称"
          width="120"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="jdnr"
          label="监督内容"
          min-width="180"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="dwmc"
          label="单位名称"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="jcdx"
          label="检查对象"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="planTime"
          label="计划开展时间"
          width="100"
          align="center"
          show-overflow-tooltip>
          <template slot-scope="scope">
            {{ scope.row.jhsjBegin + '月' + ' ~ ' + scope.row.jhsjEnd + '月' }}
          </template>
        </el-table-column>
        <el-table-column
          prop="type"
          label="监督类别"
          width="120"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="jdly"
          label="监督领域"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="jhkznr"
          label="计划开展内容"
          min-width="180"
          header-align="center">
          <template slot-scope="scope">
            <div
              style="line-height: 20px;"
              v-for="(item, index) in scope.row.jhkznrList"
              :key="index"
            >
              {{ "内容 [" + (index + 1) + "] : " + item.jhkznr }}
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="questionList"
          label="发现问题"
          min-width="180"
          header-align="center"
        >
          <template slot-scope="scope">
            <div
              style="line-height: 20px;"
              v-for="(item, index) in scope.row.questionList"
              :key="index"
            >
              {{ "问题 [ " + (index + 1) + " ] : " + item.question }}
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="questionType"
          label="问题类别"
          width="80"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="zrdw"
          label="责任单位"
          width="120"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="zrr"
          label="责任人"
          width="100"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="zgcs"
          label="整改措施"
          min-width="130"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="jhzgqk"
          label="整改情况"
          width="80"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="filePath"
          label="附件"
          width="80"
          align="center"
          show-overflow-tooltip>
          <template slot-scope="scope">
            {{ getFileName(scope.row.filePath) }}
          </template>
        </el-table-column>
        <el-table-column
          prop="isCompletedRectify"
          label="是否完成整改"
          width="100"
          align="center"
          show-overflow-tooltip>
          <template slot-scope="scope">
            {{ scope.row.isCompletedRectify ? '已完成' : '未完成' }}
          </template>
        </el-table-column>
        <el-table-column
          prop="completedRectifyDate"
          label="(预计)完成整改时间"
          width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="unfinishedRectifyReason"
          label="未完成整改原因说明"
          min-width="150"
          align="center"
          show-overflow-tooltip>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="params.currentPage"
        :page-sizes="[10, 50, 100, 200]"
        :page-size="params.pageSize"
        background
        layout="prev, pager, next, sizes, jumper, total"
        :total="params.total">
      </el-pagination>
    </div>
    <planExecution v-if="showAdd" @closeDilag="init()" :formData_in="currentRow" ></planExecution>
  </div>
</template>

<script>
  import planExecution from "../component/planExecution";
  import Interface from "@/base/mixin/risk-control-center.js";
  import SelectTree from "@/views/components/orgTreeSelect";
  export default {
    name: "supervisionPlanExecution",
    mixins: [Interface],
    props: {},
    components: { planExecution, SelectTree },
    data(){
      return {
        // 单位选择 变量
        defaultProps: {
          children: "list",
          label: "orgName",
          value: "orgId",
        },
        treeData: [],
        valueId: '',
        
        tableLoading: false,
        tableHeight: window.innerHeight - 245,
        departmentList: [],
        params: {
          pageSize: 10,
          currentPage: 1,
          total: 0,
          year: '',
          dwmc: '全部'
        },
        showAdd: false,
        formOption: {
          dwmc: [
            {
              label: '全部',
              value: '全部'
            },
            {
              label: '财务部',
              value: '财务部'
            },
            {
              label: '市场营销',
              value: '市场营销'
            }
          ],
          supervisionField: [
            {
              label: '安全监管领域',
              value: '安全监管领域'
            },
            {
              label: '市场营销领域',
              value: '市场营销领域'
            },
            {
              label: '工程建设领域',
              value: '工程建设领域'
            },
            {
              label: '大党建办公综合领域',
              value: '大党建办公综合领域'
            },
            {
              label: '财务报销领域',
              value: '财务报销领域'
            },
            {
              label: '创新与数字化领域',
              value: '创新与数字化领域'
            },
            {
              label: '其他',
              value: '其他'
            }
          ]
        },
        tableData: [],
        multipleSelection: [],
        currentRow: {}
      }
    },
    created() {
      this.init();
    },
    // 监听
    watch: {},
    mounted() {},
    methods: {
      init() {
        this.showAdd = false;
        this.currentRow = {};
        //获取年份
        this.params.year = new Date().getFullYear().toString();
        this.queryTableList(true);
        this.getOrgData();
      },
      getFileName(path) {
        let pos1 = path.lastIndexOf('/'); 
        let pos2 = path.lastIndexOf('\\'); 
        let pos = Math.max(pos1, pos2); 
        if (pos < 0) { 
          return path; 
        } 
        else { 
          return path.substring(pos + 1); 
        } 
      },
      // 查询单位名称
      getOrgData() {
        this.getSupervisionUnitTree().then((rs)=>{
          // window.console.log(rs)
          if(Array.isArray(rs) && rs.length !== 0) {
            // window.console.log(rs)
            this.treeData = rs;
          } else {
            this.treeData = [];
          }
        }).finally(()=>{});
      },
      // 多选
      handleSelectionChange(val) {
        // window.console.log(val)
        this.multipleSelection = val;
      },
      // 表格单选
      handleCurrentChange_table(val) {
        window.console.log(val)
        this.currentRow = val;
      },
      // 取值
      getValue(item){
        // window.console.log(item)
        this.params.unit = item[1];
        this.queryTableList(true)
      },
      // 查询表格内容
      queryTableList(reset) {
        if(reset) {
          this.setPages();
        }
        let params = {
          current: this.params.currentPage,
          size: this.params.pageSize,
          keyword: this.params.keyword,
          rectifyTime: this.params.rectifyTime,
          supervisionField: this.params.supervisionField,
          unit:  this.params.unit,
          year: this.params.year,
        };
        this.tableData = [];
        this.tableLoading = true;
        this.getSupervisionExecList(params).then((rs)=>{
          window.console.log(rs)
          if(rs) {
            this.tableData = rs.rows;
            this.setPages(rs.current, rs.size, rs.records);
          } else {
            this.setPages();
          }
        }).finally(()=>{
          this.tableLoading = false;
        })
      },
      // 设置页码
      setPages(currentPage = 1, pageSize = 10, total = 0) {
        this.params['pageSize'] = pageSize;
        this.params['currentPage'] = currentPage;
        this.params['total'] = total;
      },
      // 表格操作按钮点击
      handleClick(row, type) {
        window.console.log(`类型：${type} , 行内容:`, row);
        this.$router.push({
          path: '/details',
          query: {
            index: '4-2',
            params: window.btoa(window.encodeURIComponent(JSON.stringify({
              name: row.name,
              orderId: row.orderId || this.$route.query.orderId,
            }))),
            actNav: this.$route.query.actNav
          }
        });
      },
      handleSizeChange(val) {
        // window.console.log(`每页 ${val} 条`);
        this.params.pageSize = val;
        this.queryTableList();
      },
      handleCurrentChange(val) {
        // window.console.log(`当前页: ${val}`);
        this.params.currentPage = val;
        this.queryTableList();
      },
      // 计划录入
      planExecution(isEmpty = false) {
        // this.$message.success('计划录入');
        if(isEmpty) {
          this.currentRow = {};
        }
        this.showAdd = true;
      },
      // 删除列表选项
      deleteTableItem() {
        this.$confirm(`此操作将删除选中的监督计划, 是否继续?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          // window.console.log('监督计划录入表单', this.form);
          this.supervisionExecDelete({ ids: this.multipleSelection.map(item => item.jdrwId) }).then(()=>{
            this.$message.success('删除成功');
          }).finally(()=>{
            this.init();
          });
        }).catch(() => {});
      },
    }
  }
</script>

